import * as React from 'react';
import { UndoButton as Button } from '@ali/we-toolbar';
import { Controller, environment } from '@ali/4ever-cangjie';
import { HistoryRole } from '../../utils/role';

const SHORTCUTKEY = environment.IS_MAC ? 'âŒ˜+Z' : 'Ctrl+Z';

export interface UndoButtonProps {
  controller: Controller;
  locale: Record<string, string>;
}

export default function UndoButton(props: UndoButtonProps) {
  const { controller, locale, ...rest } = props;
  const isDisabled = !controller.query('canUndo');
  const handleClick = React.useCallback(() => controller.dispatch('undo'), [controller]);

  return (
    <Button
      {...rest}
      testid="bi-toolbar-undo"
      disabled={isDisabled}
      onClick={handleClick}
      shortcutText={SHORTCUTKEY}
      role={HistoryRole.undo}
    />
  );
}
